.App {
  box-sizing: border-box;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.head{
  position: relative;
  background-color: #09f;
  color: #fff;
  text-align: center;
  padding: .5rem;
}
.head_btn{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}
.head_btn > button {
  background-color: rgb(240, 165, 52);
  color: #fff;
  padding: .2rem .4rem;
  border-radius: 4px;
  border: none;
  margin: 0 .2rem;
  cursor: pointer;
}
.head_btn > button:hover{
  background-color: rgb(245, 190, 107);
}
.margin_1{
  margin: .5rem;
}
.main{
  flex: 1;
  overflow: auto;
}
.show{
  display: block;
}
.hide{
  display: none;
}
.menu{
  box-shadow: 0 0 3px rgb(240, 240, 240);
  display: flex;
}
.menu_item{
  flex: 1;
  background-color:rgb(240, 240, 240);
  color: #999;
  cursor: pointer;
  padding: .5rem 0;
  transition: 100ms;
  text-align: center;
}
.menu_item:hover{
  transition: 100ms;
  background-color: rgb(97, 192, 255);
}
.menu_item_active{
  background-color: rgb(97, 192, 255);
  color: #fff;
}
.weapon_container{
  padding: .5rem;
}
.newWeapon{
  box-shadow: 0 0 4px rgb(97, 192, 255);
  text-align: center;
  font-size: 2rem;
  color: rgb(97, 192, 255);
  margin: .4rem 0;
  cursor: pointer;
}
.weapon_box{
  box-shadow: 0 0 4px rgb(97, 192, 255);
  text-align: center;
  color: rgb(0, 149, 248);
  cursor: pointer;
  margin: .2rem 0 1.6rem 0;
  overflow: hidden;
  border-radius: 4px;
}

.newWeapon:hover{
  color: #fff;
  background-color:  rgb(97, 192, 255);
}

.mderContainer{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0,0,0,0.3);
}
.InputsBox{
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: #fff;
  padding: .5rem;
  padding-right: 1rem;
  width: 80%;
  min-height: 30%;
  max-height: 60%;
  overflow: auto;
  z-index: 100;
}

.button{
  border: none;
  outline: none;
  padding: .1rem .2rem;
  border-radius: 4px;
  cursor: pointer;
}

.button_red{
  color: #fff;
  background-color: rgb(245, 73, 73);
}
.button_blue{
  color: #fff;
  background-color: #09f;
}
.button_red:hover{
  color: #fff;
  background-color: rgb(255, 144, 144);
}
.button_blue:hover{
  color: #fff;
  background-color: rgb(139, 209, 255);
}

.righttop{
  position: absolute;
  top: .2rem;
  right: .2rem;
}

.font_small{
  font-size: .4rem;
}
.font_middle{
  font-size: .8rem;
}


.input{
  display: inline-block;
  outline: none;
  width: 65%;
  border: none;
  color: #636363;
  padding: .1rem .3rem;
  border-bottom: 1px solid rgb(0,127,212);
}
.textarea{
  outline: none;
  color: #636363;
  width: 65%;
  padding: .1rem .3rem;
  border: 1px solid rgb(0,127,212);
}
.InputsBox_item{
  margin-top: .5rem;
  display: flex;
  justify-content: space-between;
}

.InputsBox_item_font{
  display: block;
  text-align: center;
  width: 35%;
  font-size: 1rem;
}

.clobtn{
  padding: .3rem .8rem;
  margin: .2rem .5rem;
}
.InputsBox_item_btnbox{
  display: flex;
  justify-content: flex-end;
  padding: .4rem .1rem;
}
.weapon_box_btn{
  display: flex;
  cursor: pointer;
}
.weapon_box_edit{
  color: #fff;
  background-color: rgb(148, 197, 56);
  text-align: center;
  padding: .2rem 0;
  flex: 1;
  transition: 100ms;
}
.weapon_box_edit:hover{
  transition: 100ms;
  background-color: rgb(186, 233, 98);
}
.weapon_box_dele:hover{
  transition: 100ms;
  background-color: rgb(248, 142, 142);
}
.weapon_box_dele{
  padding: .2rem 0;
  color: #fff;
  text-align: center;
  flex: 1;
  transition: 100ms;
  background-color: rgb(243, 96, 96);
}

.weapon_box_head{
  display: flex;
  background-color: #09f;
  color: rgb(253, 255, 238);
  justify-content: space-between;
}
.weapon_box_head_item{
  text-align: center;
  flex: 1;
}

.weapon_box_body_title{
  border-bottom: 1px solid #09f;
}

.weapon_box_body{
  overflow: auto;
  height: 6rem;
}

.weapon_box_skill{
  background-color: rgb(44, 123, 175);
  color: #fff;
  cursor: pointer;
  transition: 100ms;
  border: 1px solid rgb(234, 247, 255);
  padding: .2rem 0;
  border-radius: 4px;
}

.weapon_box_skill:hover{
  background-color: rgb(54, 134, 187);
  transition: 100ms;
}

.input_small{
  width: 2rem;
}

.btn{
  outline: none;
  border:none;
  background-color: #09f;
  color: #fff;
  border-radius: 4px;
  transition: 100ms;
}
.InputsBox_item_rate{
  flex: 1;
  color: #0099ff;
  overflow: hidden;
}
.btn:hover{
  background-color: rgb(107, 196, 255);
  transition: 100ms;
}
.InputsBox_btn_item{
  margin: .3rem 0;
  display: flex;
  justify-content: flex-end;
}
.button_long{ 
  padding: .2rem .5rem;
  margin: .2rem;
}
.InputsBox_btn_item_list{
  position: relative;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  overflow: auto;
  padding: .2rem 1rem;
}
.right{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
}
.InputsBox_btn_item_list>div{
  margin-right: .2rem;
  padding-right: .2rem;
  white-space: nowrap;
  color: #0099ff;
}
.relic_main_key_select{
  outline: none;
  border: 1px solid #09f;
  flex: 1;
}
.input2{
  display: inline-block;
  outline: none;
  border: none;
  color: #636363;
  width: 30%;
  padding: .1rem .3rem;
  border-bottom: 1px solid rgb(0,127,212);
  flex: 1;
}
.mockcalc_sh{
  position: relative;
  z-index: 0;
}
.mockcalc_container{
  border-radius: 4px;
  background-color: rgb(75, 33, 33);
  margin: .2rem;
  box-sizing: border-box;
  overflow: auto;
  height: 500px;
  transition: 600ms;
}
.mockcalc_container_hide{
  transition: 600ms;
  height: 100px;
}
.mockcalc_container_row{
  display: flex;
  justify-content: space-between;
  padding: .1rem 1rem;
}
.mockcalc_container_row_key{
  color: #fff;
}
.mockcalc_container_row_value{
  color: #0099ff;
}

.ShowLastDataBox{
  position: absolute;
  background-color: rgba(255,255,255,0.3);
  color: #666;
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  cursor: pointer;
  color: #fff;
  bottom: 0;
  transition: 100ms;
  font-size: 12px;
}

.ShowLastDataBox:hover{
  transition: 100ms;
  color: #636363;
  background-color: rgba(255,255,255,0.6);
}

.mockcalc_title{
    margin-top: .3rem;
    text-align: center;
    color: #0099ff;
    font-weight: 900;
    font-size: 1rem;
}

.mockcalc_character_container{
  border: 1px solid #09f;
  border-radius: 4px;
  margin: .2rem;
  display: flex;
  overflow: hidden;
}
.mockcalc_character_select_box{
  width: 4rem;
  border-right: 1px solid #09f;
}
.mockcalc_character_select_box_title{
  border-bottom: 1px solid #09f;
  background-color: #09f;
  color: #fff;
  font-size: 14px;
}
.mockcalc_character_select{
  width: 100%;
}
.infobox{
  flex: 1;
  overflow: auto;
}
.info_item{
  display: inline-block;
  font-size: 12px;
  border-radius: 4px;
  margin: .2rem;
  padding: .1rem .3rem;
  background-color: rgb(230, 123, 52);
  color: #fff;
}
.weapon_effect{
  color: #0099ff;
  text-align: center;
}

.click{
  cursor: pointer;
}
.click:hover{
  background-color: #09f;
  color: #fff;
}

.cinfo_skill_container{
  border: 1px solid #09f;
  border-radius: 4px;
  margin: .4rem;
}
.cinfo_skill_title{
  background-color: #09f;
  color: #fff;
  text-align: center;
  font-size: 13px;
}
.center{
  text-align: center;
}
.skilldmg{
  overflow: auto;
  font-size: 12px;
  color: #636363;
  margin: .1rem;
  border: 1px solid #09f;
}
.relics_container{
  border: 1px solid #09f;
  border-radius: 4px;
  margin: .2rem;
  display: flex;
  padding: .4rem;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.relics_item{
  border: 1px solid #09f;
  width: 200px;
  border-radius: 4px;
  margin: 0 .2rem .2rem 0;
}
.relics_title{
  background-color: #09f;
  color: #fff;
  text-align: center;
  padding: .1rem;
  font-size: 14px;
}
.relics_head{
  display: flex;
  padding: .2rem;
  border-bottom: 1px solid #8a92ff;
  text-align: center;
}
.relics_head_left{
  width: 50%;
}
.relics_head_right{
  text-align: center;
  border-left: 1px solid #8a92ff;
  width: 50%;
}

.relics_body{
  text-align: center;
  padding:.2rem;
  color:#09f;
  font-size: 14px;
}